.loading-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px; /* Adjust height as needed */
  width: 100%;
}

/* Search Bar Styling */
.search-bar-container {
  display: flex;
  justify-content: center;
  padding: 20px 10px;
  margin-bottom: 20px;
}

.search-input-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  padding: 0 15px;
  min-height: 56px;
}

.search-input {
    width: 100%;
}

:host ::ng-deep .search-input .mat-form-field-wrapper {
    padding: 0;
}
:host ::ng-deep .search-input .mat-form-field-infix {
    border-top: none;
    padding: 8px 0;
}
:host ::ng-deep .search-input .mat-form-field-underline,
:host ::ng-deep .search-input .mat-form-field-outline {
    display: none !important;
}

.search-input .mat-input-element,
:host ::ng-deep .search-input .mat-form-field-label {
    color: black !important;
}
.search-input mat-icon {
    color: rgba(0,0,0,0.7) !important;
}

.all-course-cards-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px; /* Keep gap for spacing */
  justify-content: center;
  align-items: stretch; /* Ensures cards in the same row have equal height */
  padding: 20px; /* Add padding around the container */
}

.course-card {
  display: flex;
  flex-direction: column;
  height: 100%; /* Ensures all cards in a row try to match height */
  overflow: hidden; /* Ensure content stays within card boundaries */
  flex-basis: 300px; /* Base width for each card */
  max-width: 350px; /* Maximum width for each card */
}

.course-card .course-thumbnail-img {
  height: 200px; /* Fixed height for images */
  object-fit: cover; /* Ensure images cover the area without distortion */
  width: 100%; /* Ensure image takes full width of the card */
}

.course-card mat-card-header {
  padding-bottom: 0;
}

.course-card mat-card-title {
  font-size: 1em;
  font-weight: bold;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden; /* Hide overflow content */
  text-overflow: ellipsis; /* Add ellipsis for overflowed text */
}

.course-card mat-card-subtitle {
  font-size: 0.8em;
  color: #757575;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Limit to 2 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  min-height: 2.8em; /* Approximate height for two lines of text */
  line-height: 1.4em; /* Ensure consistent line height */
}

.course-card mat-card-content {
  flex-grow: 1; /* Allows content to expand */
  padding-top: 10px;
}

.course-card mat-card-actions {
  display: flex;
  justify-content: space-around; /* Distribute buttons with space */
  align-items: center; /* Vertically align the button */
  padding: 16px;
  min-height: 60px; /* Fixed height for consistency */
}

.course-actions {
  display: flex;
  flex-direction: row; /* Changed from column to row for desktop view */
  gap: 8px; /* Space between buttons */
  flex-shrink: 0; /* Prevent actions from shrinking */
  /* removed min-width: 150px; */
  align-items: center; /* Align items for horizontal layout */
  justify-content: flex-end; /* Push actions to the right */
}

.course-actions button {
  font-size: 0.85rem; /* Smaller font size for all buttons */
  line-height: 1.2;
  height: 36px; /* Consistent button height */
  padding: 0 10px;
}

.history-button {
  display: flex;
  align-items: center;
  justify-content: center; /* Center content in button */
}

.history-button mat-icon {
  margin-right: 5px; /* Space between icon and text */
  font-size: 18px; /* Smaller icon size */
  height: 18px;
  width: 18px;
}

.no-courses {
  text-align: center;
  padding: 40px;
  color: #999;
  font-style: italic;
}

footer {
  text-align: center;
  padding: 20px;
  margin-top: 40px;
  background-color: #f0f0f0;
  color: #666;
  border-top: 1px solid #e0e0e0;
}

/* Liquid Glass Notification Styles */
.liquid-glass-notification {
  margin: 20px auto; /* Center horizontally and provide vertical spacing */
  width: 90%;
  max-width: 400px;
  background-color: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(20px) saturate(200%);
  -webkit-backdrop-filter: blur(20px) saturate(200%);
  border-radius: 13px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  padding: 12px 18px;
  display: flex;
  align-items: center;
  color: white;
  font-family: 'SF Pro Text', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
}

.notification-content {
  display: flex;
  align-items: center;
  width: 100%;
}

.notification-image {
  width: 48px;
  height: 48px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 12px;
  flex-shrink: 0;
}

.notification-text h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  color: white;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.notification-text p {
  margin: 5px 0 0;
  font-size: 13px;
  line-height: 1.3;
  color: rgba(255, 255, 255, 0.8);
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .course-list-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .course-list-item .course-thumbnail-img {
    width: 100%;
    height: 150px;
    margin-bottom: 10px;
    margin-right: 0;
  }

  .course-details {
    width: 100%;
    margin-right: 0;
    margin-bottom: 15px;
  }

  .course-actions {
    flex-direction: row; /* Buttons side-by-side on small screens */
    width: 100%;
    justify-content: space-around;
  }

  .search-input {
    width: 95%;
  }
}

@media (max-width: 600px) {
  .liquid-glass-notification {
    width: 95%;
    padding: 12px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .notification-image {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  .notification-text h3 {
    font-size: 14px;
  }
  .notification-text p {
    font-size: 12px;
  }
}